// }

// .collapse:not(.show) {
//   display: none;
// }


.fade {
  @include transition($transition-fade);

  &:not(.show) {
    opacity: 0;
  }
}

.collapse {
  &:not(.show) {
    display: none;
  }
}

.collapsing {
  position: relative;
  height: 0;
  overflow: hidden;
  @include transition($transition-collapse);
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  height: 1px;
  width: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
}

.clearfix {
  &:before,
  &:after {
    content: "";
    display: table;
  }
  &:after {
    clear: both;
  }
}

.list-nostyled {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
  list-style: none;

  li {
    padding-left: 0;

    &::before {
      display: none;
    }
  }
}

/*      */
*:not(img)::selection {
  color: $white;
  background-color: $main-color; /* Safari, Chrome, Opera */
}

*:not(img)::-moz-selection {
  color: $white;
  background-color: $main-color; /* FireFox */
}

.container {
  display: block;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
  background: none;

  @media (min-width: $screen-md) {
    padding-left: 40px;
    padding-right: 40px;
  }

  @media (min-width: $screen-xl) {
    $padding-min: 80px; //     
    $half-screen-xl: $screen-xl / 2 - $padding-min;
    // padding: 1em calc(50% - 600px); //600px -  $screen-xl / 2 -     
    padding-left: calc(50% - #{$half-screen-xl});
    padding-right: calc(50% - #{$half-screen-xl});
  }
}

// .    : XS|SM|MD|LG|XL|XXL;
// @include row($grid-gutter-width, 10px, 12px, 16px, 20px, 26px);
.row {
  @include row(
    $xs-grid-gutter:  $grid-gutter-width,
    $sm-grid-gutter:  $grid-gutter-width,
    $md-grid-gutter:  $grid-gutter-width,
    $lg-grid-gutter:  $grid-gutter-width,
    $xl-grid-gutter:  $grid-gutter-width,
    $xxl-grid-gutter: $grid-gutter-width
  );
}

.colored {
  color: $main-color;
}

.publishedon {
  display: inline-block;
  vertical-align: baseline;
  color: $text-muted;
}

.main-content {
  //position: relative;
  //background-color: red;
}

// Slick slider custom style
.slick-arrow {
  @include text-hide;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  border: none;
  user-select: none;
  cursor: pointer;

  @media (min-width: $screen-md) {
    width: 42px;
    height: 42px;
    background-color: $link-color;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 16px 28px;
  }

  @media (min-width: $screen-xl) {
    width: 64px;
    height: 64px;
    background-size: 29px 50px;
  }

  &:hover,
  &:focus {
    background-color: $link-color--hover;
    outline: none;
  }

  &.slick-prev {
    left: 15px;
    z-index: 1;
    background-image: svg-load('../blocks/sprite-svg/svg/arrow-prev.svg', fill=$white);
  }

  &.slick-next {
    right: 15px;
    background-image: svg-load('../blocks/sprite-svg/svg/arrow-next.svg', fill=$white);
  }
}

.slider-navigation {

  .slick-current {

    img {
      display: block;
      border: 8px solid $main-color;
    }
  }
}

.sticky-wrapper.is-sticky {

  #sticker,
  #sticker-bottom {
    box-shadow: 0 15px 30px 0 rgba(138,149,158,0.2);
  }
}


